import React from 'react';
import DBP from '../../../../../assets/partners/dbp.png';
import COOP from '../../../../../assets/partners/coop_natco.png';
import LANDBANK from '../../../../../assets/partners/landbank.png';
import { useNavigate } from 'react-router-dom';
import VideoModal from '../../../components/modals/VideoModal';

export default function Main() {

    const navigate = useNavigate();
    const [showVideo, setShowVideo] = React.useState(false);
    const toggleShowVideo = () => setShowVideo(!showVideo);
    const [showOFWVideo, setShowOFWVideo] = React.useState(false);
    const toggleShowOFWVideo = () => setShowOFWVideo(!showOFWVideo);

    const handleNavigation = (e = null, path) => {
        if (e) {
            e.preventDefault();
            navigate(path);
        }
    };

    const handleSuccessVideo = (e = null) => {
        if (e) {
            e.preventDefault();
            toggleShowVideo();
        }
    }

    const handleOFWVideo = (e = null) => {
        if (e) {
            e.preventDefault();
            toggleShowOFWVideo();
        }
    }

    return (
        <div>
            {showVideo && <VideoModal url={'https://youtu.be/bYkLt2lBoCs'} open={showVideo} toggle={toggleShowVideo} />}
            {showOFWVideo && <VideoModal url={'https://youtu.be/hEphjGQ9EKM'} open={showOFWVideo} toggle={toggleShowOFWVideo} />}
            <div className="about-us-area pd-100" style={{ paddingTop: 50 }}>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6 col-md-8 align-self-center">
                            <div className="about-us-video">
                                <img className="thumb" src="assets/img/video/success.png" alt="img" height={400} width={900} />
                                <a rel="noopener" className="play-btn" href="#" data-effect="mfp-zoom-in" onClick={handleSuccessVideo}><img src="assets/img/video/play-btn.png" alt="img" /></a>
                            </div>
                        </div>
                        <div className="col-lg-5 offset-lg-1">
                            <div className="about-us-details">
                                <div className="section-title">
                                    <h2 className="title">Nothing is impossible. We can help you achieve your goals!</h2>
                                    <p>
                                        We at Bangko sa Balay Foundation Inc. continues to provide quality services and meaningful transactions that will surely help our clients.
                                    </p>
                                </div>
                                <a rel="noopener" className="btn btn-blue" href="/about">Learn More</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="about-us-area pd-100" style={{ paddingTop: 50 }}>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-5 offset-lg-1">
                            <div className="about-us-details">
                                <div className="section-title">
                                    <h2 className="title">We help our heroes, Our OFW</h2>
                                    <p>
                                        Here at Bangko sa Balay Foundation Inc., to provide their families the kind of service they deserved.
                                    </p>
                                </div>
                                <a rel="noopener" className="btn btn-blue" href="/about">Learn More</a>
                            </div>
                        </div>
                        <div className="col-lg-6 col-md-8 align-self-center">
                            <div className="about-us-video">
                                <img className="thumb" src="assets/img/video/ofw.png" alt="img" height={400} width={900} />
                                <a rel="noopener" className="play-btn" href="#" data-effect="mfp-zoom-in" onClick={handleOFWVideo}><img src="assets/img/video/play-btn.png" alt="img" /></a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="fun-fact-area">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-lg-8 text-center">
                            <div className="section-title section-title-2">
                                <h6 className="subtitle subtitle-thumb">Why Choose Us</h6>
                                <h2 className="title">Come and be part of our family</h2>
                                <p>Since we started in 2007, we have grown into a much bigger and better organization.</p>
                            </div>
                        </div>
                    </div>
                    <div className="row justify-content-center">
                        <div className="col-lg-4 col-sm-6 text-center">
                            <div className="single-fact">
                                <h1 className="counter">5000+</h1>
                                <h5>Clients</h5>
                                <p>achieve their goals</p>
                            </div>
                        </div>
                        <div className="col-lg-4 col-sm-6 text-center">
                            <div className="single-fact">
                                <h1 className="counter">10</h1>
                                <h5>Our Branches</h5>
                                <p>all over Mindanao</p>
                            </div>
                        </div>
                        <div className="col-lg-4 col-sm-6 text-center">
                            <div className="single-fact">
                                <h1 className="counter">5243+</h1>
                                <h5>Transactions</h5>
                                <p>and more...</p>
                            </div>
                        </div>
                        <div className="col-lg-12 text-center">
                            <a rel="noopener" className="btn" href="/" onClick={(e) => handleNavigation(e, "register")}>Open An Account</a>
                        </div>
                    </div>
                </div>
            </div>

            <div className="service-area default-pd">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-lg-8 text-center">
                            <div className="section-title">
                                <h6 className="subtitle subtitle-thumb">Services</h6>
                                <h2 className="title">Here are our list of Services</h2>
                                <h2 className="title">That are Right For You</h2>
                            </div>
                        </div>
                    </div>
                    <div className="row justify-content-center">
                        <div className="col-lg-4 col-md-6">
                            <div className="single-service">
                                <div className="thumb">
                                    <img src="assets/img/service/06.png" alt="img" />
                                </div>
                                <div className="service-details">
                                    <h5><a rel="noopener" href="/">Loans</a></h5>
                                    <ul>
                                        <li>Backyard broiler production project</li>
                                        <li>Small business loan</li>
                                        <li>Regular loan</li>
                                    </ul>
                                    <a rel="noopener" className="angle-btn" href="/"><img src="assets/img/icon/angle-left-round.png" alt="icon" /></a>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-6">
                            <div className="single-service">
                                <div className="thumb">
                                    <img src="assets/img/service/04.png" alt="img" />
                                </div>
                                <div className="service-details">
                                    <h5><a rel="noopener" href="/">Savings</a></h5>
                                    <ul>
                                        <li>Volountary savings</li>
                                        <li>Compulsory savings</li>
                                        <li>Progressive savings</li>
                                        <li>Kiddie savings</li>
                                        <li>Kalambuan savings</li>
                                    </ul>
                                    <a rel="noopener" className="angle-btn" href="/"><img src="assets/img/icon/angle-left-round.png" alt="icon" /></a>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-6">
                            <div className="single-service">
                                <div className="thumb">
                                    <img src="assets/img/service/02.png" alt="img" />
                                </div>
                                <div className="service-details">
                                    <h5><a rel="noopener" href="/">Microinsurance</a></h5>
                                    <ul>
                                        <li>Mutual benefit association</li>
                                        <li>Retirement savings fund</li>
                                    </ul>
                                    <a rel="noopener" className="angle-btn" href="/"><img src="assets/img/icon/angle-left-round.png" alt="icon" /></a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="partners-area default-pd" style={{ marginTop: 0, minHeight: 500, backgroundColor: "#FFFFFF !important" }}>
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-lg-8 text-center">
                            <div className="section-title">
                                <h6 className="subtitle subtitle-thumb">Partners</h6>
                                <h2 className="title">Our partners</h2>
                            </div>
                        </div>
                    </div>
                    <div className="row justify-content-center">
                        <div className="col-lg-4 col-md-12 text-center">
                            <img src={DBP} alt="DBP Logo" className="d-lg-inline-block d-xs-none" />
                            <img src={DBP} alt="DBP Logo" className="d-sm-none" width={150} height={150} style={{ margin: 5 }} />
                        </div>
                        <div className="col-lg-4 col-md-12 text-center">
                            <img src={COOP} alt="COOP Natco Logo" className="d-lg-inline-block d-xs-none" />
                            <img src={COOP} alt="COOP Natco Logo" className="d-sm-none" width={450} height={100} style={{ margin: 5, marginTop: 40 }} />
                        </div>
                        <div className="col-lg-4 col-md-12 text-center">
                            <img src={LANDBANK} alt="Landbank Logo" className="d-lg-inline-block d-xs-none" />
                            <img src={LANDBANK} alt="Landbank Logo" className="d-sm-none" width={450} height={250} style={{ margin: 5, marginTop: 40 }} />
                        </div>
                    </div>
                </div>
            </div>
        </div >
    )
}
