import React from 'react'
// Modal Components
import { Box, Button, Dialog, DialogContent } from '@mui/material'
import ReactPlayer from 'react-player'
import { FaTimes } from 'react-icons/fa'

export default function VideoModal(props) {

    return (
        <div>
            <Dialog
                open={props.open}
                onClose={props.toggle}
                fullWidth={true}
                fullScreen={true}
                sx={{
                    zIndex: 9999,
                    left: 50,
                    '.MuiDialog-paper': {
                        background: 'transparent',
                        boxShadow: 'none'
                    }
                }}
            >
                <DialogContent>
                    <Box sx={{ textAlign: 'center' }}>
                        <Button onClick={props.toggle} color='secondary' variant="contained" sx={{ zIndex: 3000, position: 'fixed', top: 5, right: 120 }}>Close <FaTimes /></Button>
                        <ReactPlayer
                            className='react-player fixed-bottom'
                            url={props.url}
                            width="100%"
                            height="100%"
                            controls={true}
                        />
                    </Box>
                </DialogContent>
            </Dialog>
        </div >
    )
}
